@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  padding: 0;
  margin: 0;
}

.nav-bar {
  width: 100%;
  background-image: linear-gradient(#323232, #303131);
  display: flex;
  justify-content: space-between;
  position: fixed;
}

.nav-logo {
  background-image: linear-gradient(#323232, #303131);
  width: 250px;
  height: 47px;
}

.img-logo {
  width: 120px;
  height: 115px;
  margin-top: -32px;
  margin-left: 95px;
}

ul {
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  display: flex;
  padding-right: 50px;
}

li a {
  display: block;
  padding: 15px 40px;
  color: white;
  text-decoration: none;
  font-weight: 400;
  font-size: 15px;
}

li a:hover {
  background-color: #1a1a1a;
  background-image: linear-gradient(#2a2a2a, #1a1a1a);
}

@media screen and (max-width: 768px) {
  .nav-bar {
    flex-direction: column;
    align-items: center;
  }

  .nav-logo {
    width: 100%;
    text-align: center;
  }

  .img-logo {
    margin: 3px auto;
  }

  ul {
    flex-direction: column;
    padding: 0;
    width: 100%;
  }

  li a {
    text-align: center;
    padding: 10px 0;
  }
}

@media screen and (max-width: 480px) {
  .img-logo {
    width: 50px;
    height: 32px;
  }

  li a {
    font-size: 14px;
  }
}

.body-home {
  background-color: #f2f2f2;
  background-image: linear-gradient(#f2f2f2, #dbdbdb);
  width: 100%;
  height: 100vh;
}

.text-home {
  width: 600px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding-top: 8%;
  text-align: center;
}

.txt1 {
  font-size: 4em;
  letter-spacing: -1px;
  color: #FF355B;
  font-weight: 500;
}

.txt2 {
  font-size: 7em;
  width: 100%;
  letter-spacing: -4px;
  color: #E64A7F;
  font-weight: 500;
}

.txt3 {
  font-size: 7em;
  width: 100%;
  letter-spacing: -4px;
  color: #c187dc;
  font-weight: 500;
}

.txt4 {
  font-size: 3.3em;
  color: #375AE4;
  font-weight: 500;
  letter-spacing: -1.5px;
  width: 100%;
}

.txt1:hover {
  color: #ff355abe;
  cursor: default;
}

.txt2:hover {
  color: #e64a7ec4;
  cursor: default;
}

.txt3:hover {
  color: #c187dcd7;
  cursor: default;
}

.txt4:hover {
  color: #375ae4c2;
  cursor: default;
}

.btn-home {
  margin: 0 auto;
  margin-top: 70px;
  width: 100%;
  padding: 20px 30px;
  background-color: #a1b7b831;
  border: 0;
  color: #0c6d70;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
}

.btn-home a {
  text-decoration: none;
  color: inherit;
}

.btn-home:hover {
  background-color: #04c2c98f;
  cursor: pointer;
  transition: ease 0.5s;
  color: white;
}

@media screen and (max-width: 1024px) {
  .text-home {
    width: 90%;
    padding-top: 15%;
  }

  .txt1 {
    font-size: 3.5em;
  }

  .txt2, .txt3 {
    font-size: 6em;
  }

  .txt4 {
    font-size: 3em;
  }
}

@media screen and (max-width: 768px) {
  .text-home {
    padding-top: 20%;
  }

  .txt1 {
    font-size: 3em;
  }

  .txt2, .txt3 {
    font-size: 5em;
  }

  .txt4 {
    font-size: 2.5em;
  }

  .btn-home {
    width: 80%;
  }
}

@media screen and (max-width: 480px) {
  .text-home {
    padding-top: 25%;
  }

  .txt1 {
    font-size: 2.5em;
  }

  .txt2, .txt3 {
    font-size: 4em;
  }

  .txt4 {
    font-size: 2em;
  }

  .btn-home {
    width: 90%;
    padding: 15px 20px;
    font-size: 14px;
  }
}
.body-contato {
  width: 100%;
  min-height: 110vh;
  background-color: #252934;
}

.body-triang {
  width: 100%;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  overflow: auto;
}

.triang1 {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  width: 0; 
  height: 0; 
  border-left: 400px solid transparent;
  border-right: 400px solid transparent;
  border-top: 100px solid #dbdbdb;
}

.h1-contato {
  margin: 0 auto;
  color: white;
  margin-top: 100px;
  margin-bottom: 60px;
  width: 170px;
  border-bottom: 5px solid white;
  line-height: 60px;
  text-align: center;
}

.body-input {
  width: 500px;
  height: 230px;
  margin: 0 auto;
}

.form {
  display: flex;
  flex-direction: column;
}

.form input {
  height: 38px;
  margin-top: 6px;
  background-color: black;
  border: 0;
  padding-left: 15px;
  font-size: 16px;
  color: white;
}

textarea {
  background-color: black;
  border: 0;
  padding-left: 15px;
  font-size: 16px;
  color: white;
  margin-top: 6px;
  padding-top: 8px;
  max-width: 97%;
}

h3 {
  color: #04c2c9;
  font-family: Raleway, Arial;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  text-align: center;
  margin-bottom: 25px;
}

.body-links {
  width: 30%;
  margin: 0 auto;
  margin-top: 160px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.icons {
  color: #D8E4E9;
}

.icons:hover {
  color: #27acb1;
  cursor: pointer;
  transition: ease 0.5s;
}

.btn-email {
  margin: 0 auto;
  margin-top: 30px;
  width: 25%;
  padding: 10px 25px;
  background-color: #252934;
  border: 1px solid #04c2c9;
  color: #04c2c9;
  font-size: 15px;
}

.btn-email:hover {
  background-color: #04c2c98f;
  cursor: pointer;
  transition: ease 1s;
  color: white;
}

@media screen and (max-width: 1024px) {
  .body-input {
    width: 80%;
  }

  .body-links {
    width: 50%;
  }

  .triang1 {
    border-left: 300px solid transparent;
    border-right: 300px solid transparent;
  }
}

@media screen and (max-width: 768px) {
  .body-input {
    width: 90%;
  }

  .body-links {
    width: 70%;
  }

  .triang1 {
    border-left: 200px solid transparent;
    border-right: 200px solid transparent;
  }

  .btn-email {
    width: 40%;
  }
}

@media screen and (max-width: 480px) {
  .body-input {
    width: 95%;
  }

  .body-links {
    width: 90%;
  }

  .triang1 {
    border-left: 150px solid transparent;
    border-right: 150px solid transparent;
    border-top: 80px solid #dbdbdb;
  }

  .h1-contato {
    font-size: 24px;
    width: 150px;
  }

  .btn-email {
    width: 60%;
    font-size: 14px;
  }

  .form input, textarea {
    font-size: 14px;
  }
}
.body-sobre {
  width: 100%;
  min-height: 100vh;
  background-color: rgba(185, 207, 216, 0.554);
  padding-bottom: 150px;
}

.h1-sobre {
  margin: 0 auto;
  padding-top: 100px;
  text-align: center;
  color: rgb(5, 52, 129);
  width: 110px;
  border-bottom: 5px solid rgb(5, 52, 129);
  line-height: 60px;
  text-align: center;
}

.body-perfil {
  width: 67%;
  min-height: 200px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 0 auto;
  margin-top: 5%;
  background-color:rgba(255, 255, 255, 0.404);
}

.foto-perfil {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.foto-perfil img {
  width: 90%;
}

.texto-perfil {
  font-family: 'Raleway', cursive;
  font-size: 18px;
  line-height: 27px;
  margin: 4% 20px;
  letter-spacing: 2px;
}

.container-figuras {
  width: 70%;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0 auto;
  margin-top: 5%;
}

.body-habilidades {
  background-color:rgba(255, 255, 255, 0.404);
  padding: 30px 0;
  width: 67%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 70px;
  align-content: center;
  align-items: center;
  box-shadow: 1px 2px 2px rgba(115, 117, 122, 0.054);
}

.icons-sobre {
  color: rgba(61, 140, 204, 0.562);
  margin-left: 60px;
  margin-right: 60px;
}

.figuras {
  width: 160px;
  padding: 30px 35px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.404);
  
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 2px 2px rgba(115, 117, 122, 0.054);
  
}

.figuras:hover {
  box-shadow: 5px 8px 8px rgba(115, 117, 122, 0.054);
  
}

.figuras h2 {
  font-family: 'Raleway', cursive;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #045043;
}

.figuras p {
  font-family: 'Raleway', cursive;
  font-size: 16px;
  line-height: 28px;
  color: black;
  font-weight: 500;
  letter-spacing: 0.4px;
  text-align: center;
}

/* Media Queries para Responsividade */
@media screen and (max-width: 1024px) {
  .body-perfil {
    width: 85%;
  }

  .container-figuras {
    width: 85%;
    flex-wrap: wrap;
  }

  .figuras {
    margin-bottom: 20px;
  }

  .body-habilidades {
    width: 85%;
    flex-wrap: wrap;
  }

  .icons-sobre {
    margin: 20px;
  }
}

@media screen and (max-width: 768px) {
  .body-perfil {
    flex-direction: column;
    align-items: center;
  }

  .foto-perfil {
    width: 100%;
    margin-bottom: 20px;
  }

  .texto-perfil {
    width: 90%;
    margin: 20px auto;
    text-align: center;
  }

  .container-figuras {
    flex-direction: column;
    align-items: center;
  }

  .figuras {
    width: 80%;
    margin: 10px 0;
  }

  .body-habilidades {
    flex-direction: column;
    align-items: center;
  }

  .icons-sobre {
    margin: 15px 0;
  }
}

@media screen and (max-width: 480px) {
  .h1-sobre {
    font-size: 24px;
    width: 90px;
  }

  .texto-perfil {
    font-size: 16px;
    line-height: 24px;
  }

  .figuras h2 {
    font-size: 20px;
  }

  .figuras p {
    font-size: 14px;
    line-height: 22px;
  }
}
